.breadcrumbs {
  color: $gray;
  .is-active {
    pointer-events: none;
    color: $gray;
    text-decoration: none;
    @include before {
      display: none; } }
  li {
    display: inline-block; }
  a {
    font-size: $fz-sm;
    color: $white;
    position: relative;
    @include before {
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
      border-bottom: 1px solid  $gray; } } }
