h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {
  font-size: 36px;
  color: $white;
  @include bold;
  line-height: 1.2;
  text-transform: uppercase; }

.h2, h2 {
  text-transform: uppercase;
  color: $white;
  @include bold;
  font-size: 60px;
  line-height: 1.2;
  @include css-lock(40, 60, 576, 1199, font-size); }


.h2-subttl {
  font-size: 32px;
  @include thin;
  color: $white;
  line-height: 1.2;
  @include css-lock(24, 32, 576, 1199, font-size); }

.h3, h3 {
  @include bold;
  @include css-lock(28, 38, 576, 1199, font-size);
  line-height: 1;
  &--blue {
    color: $blue; } }

.h3-subttl {
  font-size: 21px;
  color: $black;
  text-transform: uppercase;
  @include light;
  @include css-lock(18, 21, 576, 1199, font-size);
  line-height: 1; }

.h4, h4 {
  text-transform: uppercase;
  @include light;
  color: $white;
  font-size: 18px;
  @include sm {
    font-size: 16px; } }

.h5, h5 {}

.h6, h6 {}
