.hero {
  @include cover;
  min-height: 50vh;
  padding-top: $header-height;
  @include flex(center, center);
  position: relative;
  opacity: 0;
  @include before {
    @include coverdiv;
    background-color: rgba($black, .4);
    z-index: 0; }
  @include lg {
    padding-top: $header-height-mob; }
  &__block {
    text-align: center;
    position: relative;
    z-index: 1; }
  &__title {
    margin-bottom: 10px;
    opacity: 0; }
  &__breadcrumbs {
    opacity: 0; } }
